import { CommonModule } from '@angular/common';

import { ListSubActivityRoutingModule } from './list-sub-activity-routing.module';
import { ListSubActivityComponent } from './list-sub-activity.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CoreModule } from 'core/core.module';

@NgModule({
  imports: [ListSubActivityRoutingModule,
CommonModule,
    FormsModule,
    ReactiveFormsModule,CoreModule
  ],
  declarations: [ListSubActivityComponent]
})
export class ListSubActivityModule { }



