import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ListSubActivityComponent } from './list-sub-activity.component';

const routes: Routes = [{ path: '', component: ListSubActivityComponent }];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ListSubActivityRoutingModule { }



