import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Modal, overlayConfigFactory } from 'angular2-modal';
import { BSModalContext } from 'angular2-modal/plugins/bootstrap';
import { ConfirmpopupComponent } from 'classes/confirmationPopup/confirmationPopup.component';
import { Constants } from 'core/constants';
import { HttpCommonUtilsService } from 'core/http_wrapper/http-common-utils.service';
import { HttpUserAuthUtilsService } from 'core/http_wrapper/http-userauth-utils.service';
import { LocalStorageService } from 'core/service/local-storage.service';

@Component({
  selector: 'app-list-sub-activity',
  templateUrl: './list-sub-activity.component.html',
  styleUrls: ['./list-sub-activity.component.css']
})
export class ListSubActivityComponent implements OnInit {
  subcategoryDataFromApi=[];
  headerTitleGYMRegular=[{name:"Sr No",view:false,id:0},{name:"Main Activity",view:false,id:0},{name:"Special Activity",view:false,id:0},{name:"Edit",view:false,id:0},{name:"Delete",view:false,id:0}]
  activityToDelete: any;

  constructor(private http_common: HttpCommonUtilsService,private _userAuthHttpService: HttpUserAuthUtilsService,private route: ActivatedRoute,private router: Router,private http_userauth: HttpUserAuthUtilsService,private modal: Modal) { }

  ngOnInit() {
    this._userAuthHttpService.getAllSubCategory(this,true);
  }
  
  navigatetoAddSubActivity(){
    this.router.navigate(['manageSubCategory'])

  }
  navigatetoEditSubActivity(id){
    this.router.navigate(['manageSubCategory/'+id])

  }
  deleteActivity(item){
    this.activityToDelete = item;
    this.openconfirmpopup("Do you really want to remove "+item.name+" ?","Are you Sure?","Yes","No",this,"PACKAGE_DELETE")
  
  }
     
  openconfirmpopup(message, title,yesTitle,noTitle, parent?: any, errorCode?: any) {
    return this.modal.open(ConfirmpopupComponent, overlayConfigFactory({ message, title ,yesTitle,noTitle, parent, errorCode}, BSModalContext));

  }
  confirmAction(){
  
    let request={"name":this.activityToDelete.name,
    "classCategoryId":this.activityToDelete.classCategoryResponseDto.id,
    "packageType":this.activityToDelete.packageType,
    "userId":LocalStorageService.getUserId(),
    "status":false

    
    }
    this._userAuthHttpService.editSubCategory(this.activityToDelete.id,request,this,true)

  }
  cancelAction(){
    }  
  
  onSuccess(type: any, responsedata: any) {
    
    if(type == Constants.getSubCategoryApi){
      this.subcategoryDataFromApi = responsedata.data;
     
    }
      
    if(type == Constants.EditSubCategoryApi){
      this._userAuthHttpService.getAllSubCategory(this,true);
     
    }
   
  }
  onFailure(type: any, response: any) {
    if(type == Constants.getSubCategoryApi){
     
    }
    if(type == Constants.EditSubCategoryApi){
     
    }
    
  }
}



